nav ul {
  margin: 20px 20px 0 20px;
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-direction: row;
}
nav li a {
  display: block;
  width: 50px;
  text-align: center;
  color: #aaa;
  cursor: pointer;
}
nav li a:hover {
  color: #444;
}
nav li a.active {
  color: #44f;
}
#card {
  box-shadow: 9px 7px 40px -6px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  width: 500px;
  padding: 0;
  height: 500px;
  min-height: 300px;
  margin: 20px;
  border-radius: 5px;
}

#card .details {
  top: 0;
  left: 0;
  right: 0;
  padding: 16px 20px;
  color: #888;
  display: flex;
  flex-direction: row;
}
.thunder #card .details {
  color: #ddd;
}
#card .details .right {
  text-align: right;
}
#card .details #date {
  margin: 4px 0;
}

.weather2 {
  background-color: none;
  -webkit-transition: background-color 2s ease;
  transition: background-color 2s ease;
}
.weather {
 opacity: 0.5;
 overflow:hidden;
 border-radius: 5px;
 z-index:1;
}

.weather:before {
 content:'';
 background: inherit; 
 position: relative;
 box-shadow: inset 0 0 200px rgba(255,255,255,0.05);
 top:0;
 right:0;
 left:0;
 bottom:0;
}
.thunder .weather {
  background-color: #9fa4ad;
}
.rain .weather {
  background-color: #d8d8d8;
}
.sun .weather {
  background-color: #ccf;
}
.weather #inner {
  background-color: rgba(255, 255, 255, 1);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0)));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0) 100%);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0) 100%);
}
.weather .cloud {
  -webkit-transition: fill 2s ease;
  transition: fill 2s ease;
}
.weather #cloud1 {
  fill: #efefef;
}
.thunder .weather #cloud1 {
  fill: #9fa4ad;
}
.weather #cloud2 {
  fill: #e6e6e6;
}
.thunder .weather #cloud2 {
  fill: #8b8e98;
}
.weather #cloud3 {
  fill: #d5d5d5;
}
.thunder .weather #cloud3 {
  fill: #7b7988;
}
#outer, #back {
  position: fixed;
  pointer-events: none;
}

.github-corner:hover
.octo-arm {
	-webkit-animation: octocat-wave 560ms ease-in-out;
	        animation: octocat-wave 560ms ease-in-out;
}

@-webkit-keyframes octocat-wave {
	0%, 100% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}

	20%, 60% {
		-webkit-transform: rotate(-25deg);
		        transform: rotate(-25deg);
	}

	40%, 80% {
		-webkit-transform: rotate(10deg);
		        transform: rotate(10deg);
	}
}

@keyframes octocat-wave {
	0%, 100% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}

	20%, 60% {
		-webkit-transform: rotate(-25deg);
		        transform: rotate(-25deg);
	}

	40%, 80% {
		-webkit-transform: rotate(10deg);
		        transform: rotate(10deg);
	}
}

@media (max-width: 500px) {
	.github-corner:hover .octo-arm {
		-webkit-animation: none;
		        animation: none;
	}

	.github-corner
.octo-arm {
		-webkit-animation: octocat-wave 560ms ease-in-out;
		        animation: octocat-wave 560ms ease-in-out;
	}
}


